.tabs-nav {
  display: flex;
  justify-content: flex-start;
  width: fit-content;
}

.tabs-nav__item {
  border-bottom: 3px solid transparent;
  cursor: pointer;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
  font-size: 24px;
  opacity: 0.5;
  user-select: none;
  margin-left: 50px;
  white-space: nowrap;
}

.tabs-nav__item:nth-child(1) {
  margin-left: 0px;
}

.tabs-nav__item.is-active {
  border-color: #19A8D5;
  opacity: 1;
}

.tab {
  display: none;
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
  top: 0;
  left: 0;
}

.tab.is-active {
  opacity: 1;
  transition: opacity 0.5s ease;
  display: block;
  pointer-events: auto;
}

.tab span {
  color: #19A8D5;
}

.tab__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.tabs {
  position: relative;
  overflow: hidden;
}

.tabs__content {
  padding-top: 100px;
  position: relative;
}

.wrapper__left {
  margin-right: 78px;
}

.wrapper__left p {
  margin-top: 25px;
}

.programm-item {
  margin-top: 60px;
}

.h2-num {
  font-family: 'Cinzel', serif;
  color: inherit !important;
}

.price__wrapper h3 {
  margin-top: 25px;
}

.price__items {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.price__item {
  display: flex;
  align-items: center;
  line-height: 1em;
  flex-basis: 26%;
  margin-top: 40px;
}

.price__item p {
  margin-left: 25px;
}

.icon {
  display: block;
  margin: 0 auto;
  fill: #292B33;
}

.slideshow {
  position: relative;
  width: 100%;
  overflow: hidden;
  margin: 0;
  height: 74vh;
}

.slideshow::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.4);
  z-index: 1000;
  opacity: 0;
  transition: opacity 0.4s;
}

.slide {
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  position: absolute;
  overflow-x: hidden;
  pointer-events: none;
  opacity: 0;
}

.slide--current {
  opacity: 1;
  z-index: 999;
  pointer-events: auto;
  position: relative;
}

.slide__wrap {
  width: 100%;
  overflow: hidden;
  height: 100%;
  position: absolute;
}

.slide__bg,
.slide__img {
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: white;
  background-size: cover;
  position: absolute;
  pointer-events: none;
  background-position: 50% 0%;
}

.boxnav {
  position: absolute;
  display: flex;
  z-index: 1000;
  width: 100%;
  top: 50%;
  transform: translateY(-50%);
  justify-content: space-between;
  padding-left: 40px;
  padding-right: 40px;
}

.boxnav__item {
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  border: 0;
  left: auto;
  right: 0;
  width: 47px;
  height: 47px;
  border-radius: 50%;
  flex: none;
  font-family: 'Avenir Medium', sans-serif;
  font-weight: 500;
}

.boxnav__item:focus {
  outline: none;
}

.boxnav__item svg {
  display: block;
}

.boxnav__item--prev:hover,
.boxnav__item--next:hover {
  color: #858585;
}

.boxnav__label {
  padding: 0 0.5rem;
  font-size: 1.15rem;
  position: relative;
}

.boxnav__label--total::before {
  content: '';
  position: absolute;
  right: 100%;
  top: 0;
  width: 2px;
  height: 1.45rem;
  background: #000;
  transform: rotate(22.5deg);
}

.icon--caret-rot {
  transform: rotate(180deg);
}

.gallery__wrapper h2 {
  margin-bottom: 60px;
}

.gallery__item {
  position: relative;
}

@media (max-width: 1730px) {
  .header__content {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .container {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .strip {
    margin-right: 10vw;
  }
  .strip-nav {
    left: -5vw;
  }
  .nav {
    padding-left: 10vw;
    padding-right: 10vw;
  }
  .tour-h1 {
    font-size: 60px;
  }
  .white-block {
    padding: 50px 70px;
  }
}

