    margin-right: 0px;
  }
}

@media (max-width: 1095px) {
  body {
    font-size: 18px;
  }
  .info-block {
    display: none;
  }
  h1 {
    font-size: 80px;
  }
  .header__content__description {
    font-size: 20px;
    margin-top: 30px;
  }
  h2 {
    font-size: 45px;
  }
  h3 {
    font-size: 20px;
  }
  .bottom-content.welcome img {
    max-width: 75%;
  }
  .bottom-content.welcome img:nth-child(2) {
    max-width: 50%;
  }
  .tour-h1 {
    font-size: 48px;
  }
  .white-block {
    width: 100%;
    background: rgba(255, 255, 255, 0.7);
  }
  .container {
    padding-top: 60px;
  }
  .container.last {
    padding-bottom: 60px;
  }
  .tour-header__tour-content {
    margin-top: -60px;
  }
  .price__item {
    flex-basis: 50%;
  }
}

@media (max-width: 850px) {
  .choose-items__item {
    flex-basis: 100%;
    margin-right: 0px;
  }
  .bottom-content.welcome img {
    max-width: 100%;
  }
  .bottom-content.welcome img:nth-child(2) {
    display: none;
  }
  .top-content {
    width: 100%;
  }
  .main-footer__content {
    flex-direction: column;
  }
  .main-footer__content__left-content, .main-footer__content__right-content {
    width: 100%;
  }
  .main-footer__content__right-content {
    margin-top: 30px;
  }
  .bottom-content {
    margin-top: 40px;
  }
  .img-outer {
    height: 221px;
    width: 284px;
  }
  .strip-nav__line {
    width: 100%;
    height: 1px;
    background: rgba(41, 43, 51, 0.2);
  }
  .input-wrapper {
    margin-top: 40px;
  }
  .waves-wrapper {
    bottom: -35vh;
  }
  .footer__container {
    padding-bottom: 60px;
  }
  .strip-nav {
    width: 100%;
    padding-right: calc(5vw + 20px);
  }
  .strip-nav__left {
    margin-right: 20px;
  }
  .strip-nav__right {
    margin-left: 20px;
  }
  .nav__items {
    pointer-events: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    opacity: 0;
    transform: translateX(100%);
    transition: transform 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95), opacity 0.5s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }
  .nav__items__wrapper {
    height: 100%;
    width: 100%;
    flex-direction: column;
    background: #54555C;
    align-items: center;
    text-align: center;
    padding-top: 100px;
    padding-bottom: 100px;
  }
  .nav__items.show {
    pointer-events: auto;
    opacity: 1;
    transform: translateX(0px);
  }
  .nav__item {
    margin-left: 0px;
    transform: translateX(100%);
    transition: transform 0.5s cubic-bezier(0.645, 0.045, 0.355, 1);
  }
  .nav__item.active {
    transform: translateX(0%);
  }
  .nav__item.active:nth-child(1) {
    transition-delay: 0.08s;
  }
  .nav__item.active:nth-child(2) {
    transition-delay: 0.16s;
  }
  .nav__item.active:nth-child(3) {
    transition-delay: 0.24s;
  }
  .nav__item.active:nth-child(4) {
    transition-delay: 0.32s;
  }
  .nav__item a {
    color: white;
  }
  .strip-link {
    font-size: 18px;
  }
  .burger {
    display: block;
  }
  .strip-after {
    width: 95%;
  }
  .draggable {
    display: none;
  }
  .strip {
    pointer-events: auto;
  }
  .strip-outer {
    overflow-x: scroll;
  }
  .strip-nav {
    display: none;
  }
  .dropbtn {
    color: white;
  }
  .dropdown-content {
    background: transparent;
    margin-top: 10px;
    position: relative;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
  .main-footer__content__left-content__contacts-items.float-right {
    align-items: flex-start;
  }
  .social-media a {
    margin-left: 0px;
    margin-right: 20px;
  }
  .white-block {
    flex-direction: column;
    padding: 18px 70px;
  }
  .white-block__item {
    margin-left: 0px;
    margin-top: 24px;
    line-height: 1em;
  }
  .white-block__item.first {
    margin-top: 0px;
  }
  .tour-header__tour-content {
    width: 100%;
  }
  .tour-h1 {
    font-size: 36px !important;
  }
  .price__item {
    flex-basis: 100%;
  }
}

